export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
      html
      frontmatter {
        date
        title
        tags
        main_text
        main_image {
          childImageSharp {
            
            fixed(              
              quality: 80 
              width: 800                        
            ) {
              ...GatsbyImageSharpFixed_noBase64
            }            
          }
        }
        cover {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 900         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        youtube_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        itch_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        unitystore_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        docs_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 200         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }

      }
    }
  }
`;
